<template>
  <div class="address">
    <!-- 公用头部 -->
    <Header :isLeft="true" title="保存收货地址" />
    <!-- 地址页面搜索 -->
    <div class="city_search">
        <!-- 搜索行 -->
        <div class="search">
            <span class="city" @click="$router.push('/city')">
                {{city}}
                <i class="fa fa-angle-down"></i>
            </span>
            <i class="fa fa-search"></i>
            <input type="text" v-model="search_val" placeholder="小区/写字楼/学校等">
        </div>
        <!-- 当前定位信息 -->
        <Location @click="selectAddress" :address="address" />
    </div>
    <!-- 搜索地点相关列表 -->
    <div class="area">
        <ul class="area_list">
        <!-- 视频课上对ul遍历，此处修改为遍历li -->
        <li @click="selectAddress(item)" v-for="(item,index) in areaList" :key="index">
          <h4>{{item.name}}</h4>
          <p>{{item.district}}{{item.address}}</p>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import Header from "../components/Header.vue";
import Location from "../components/Location.vue";
export default {
    name: "Address",//用小写开头会报错（警告），可能是命名冲突
    data(){
        return {
            city: "",
            search_val: "",
            areaList: [],
        }
    },
    computed: {
        address() {
            return this.$store.getters.location.formattedAddress;
        }
    },
    watch: {
        search_val() {
            this.searchPlace();
        }
    },
    methods: {
        searchPlace(){
            const self = this;
            //console.log(this.search_val);
            //高德地图实现了输入提示
            AMap.plugin("AMap.Autocomplete",function() {
                //实例化Autocomplete
                var autoOptions = {
                    //city限定城市，默认国家
                    city: self.city
                };
                var autoComplete = new AMap.Autocomplete(autoOptions);
                autoComplete.search(self.search_val,function(status, result){
                    //搜索成功时，result即是对应的匹配数据
                    // console.log(result);
                    self.areaList = result.tips;
                });
            });
        },
        selectAddress(item){//别忘了传参数
            //设置地址
            if(item){
              this.$store.dispatch("setAddress",item.district+item.address+item.name);
            }else{
              this.$store.dispatch("setAddress",this.address);
            }
            //跳转到主页
            this.$router.push("/home");
        }
    },
    components: {
        Header,
        Location
    },
    beforeRouteEnter(to,from,next){
        //进入到当前组件之前，用to来获取传过来的内容（而不是from）
        // console.log(to);//注意，前面home页面的地址如果还没加载出来的话，此处就会报错，因为地址的city还没获取到
        next(vm => {
            vm.city = to.params.city;
        });
    }
}
</script>

<style scoped>
.address {
  width: 100%;
  height: 100%;
  overflow: auto;
  box-sizing: border-box;
  padding-top: 45px;
}

.city_search {
  background-color: #fff;
  padding: 10px 20px;
  color: #333;
}

.search {
  background-color: #eee;
  height: 40px;
  border-radius: 10px;
  box-sizing: border-box;
  line-height: 40px;
}
.search .city {
  padding: 0 10px;
}
.city i {
  margin-right: 10px;
}
.search input {
  margin-left: 5px;
  background-color: #eee;
  outline: none;
  border: none;
}

.area {
  margin-top: 16px;
  background: #fff;
}
.area li {
  border-bottom: 1px solid #eee;
  padding: 8px 16px;
  color: #aaa;
}
.area li h4 {
  font-weight: bold;
  color: #333;
  margin-bottom: 5px;
}
</style>